import Vue from 'vue'

Vue.component('previsao-table', {
  props: {
    responseData: null
  },
  template: `<div v-if="responseData">
    <h2>{{ responseData.nome }}</h2>
    <ul>
      <li>Vagas: {{ responseData.vagas }}</li>
      <li>Requisies: {{ responseData.requisicoes }}</li>
      <li>Quantidade de alunos usando a extenso que solicitaram matrcula nesta disciplina: {{ responseData.extensao }}</li>
      <li>Posio do aluno com solicitao indeferida, considerando os usurios da extenso que solicitaram matrcula nesta disciplina: {{ responseData.posicao }}</li>
      <li>Critrio de corte: {{ responseData.criterio | upperCase }}</li>
      <span v-if="responseData.corte">
        <li>O aluno com solicitao indeferida tem reserva de vaga? {{ responseData.corte.reserva ? 'Sim' : 'No' }}</li>
        <li>CP do aluno com solicitao indeferida: {{ responseData.corte.cp }}</li>
        <li>CR do aluno com solicitao indeferida: {{ responseData.corte.cr }}</li>
        <li>IK do aluno com solicitao indeferida: {{ responseData.corte.ik }}</li>
      </span>
    </ul>
  </div>`,
  filters: {
    upperCase: str => {
      if (str) {
        return str.toUpperCase()
      }
    }
  }
})



